$height: 46px;

.select {
  width: 100%;
  position: relative;
  z-index: 10;

  &.open {
    z-index: 11;
    .select__dropdown {
      display: block;
    }

    .select__input {
      border-bottom: none;
    }

    .select__backdrop {
      display: block;
    }
  }

  &__input {
    border: 1px solid #ccc;
    border-radius: 5px;
    height: $height;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1rem;
    cursor: pointer;
    background-color: #fff;
  }

  &__dropdown {
    display: none;
    position: absolute;
    border: 1px solid #ccc;
    border-radius: 5px;
    top: $height;
    left: 0;
    right: 0;
    max-height: 200px;
    overflow-y: auto;
    box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.3);
  }

  &__list {
    padding: 0;
    margin: 0;
    list-style: none;
    background-color: #fff;
  }

  &__backdrop {
    position: fixed;
    display: none;
    z-index: -1;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: transparent;
  }

  &__item {
    padding: 1rem;
    border-bottom: 1px solid #ccc;

    &-multiple {
      padding: 1rem;
      border-bottom: 1px solid #ccc;

      &.selected{
        background-color: bisque;
      }
      
      &:hover {
        background-color: #eee;
        cursor: pointer;
        transition: 0.15s background-color ease-in;
      }
    }

    &.selected {
      background-color: bisque;
    }

    &:hover {
      background-color: #eee;
      cursor: pointer;
      transition: 0.15s background-color ease-in;
    }
  }
}
